.toc {
  grid-column: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;

  .film-title {
    box-sizing: border-box;
    width: 100%;
    font-size: 16px;
    padding: 5px;
    cursor: pointer;
    transition: color, background-color, 0.4s;
    color: rgb(178, 186, 194);
    border-radius: 5px;
    &:hover {
      color: rgb(255, 255, 255);
      background-color: rgba(19, 47, 76, 0.4);
    }
    &.selected {
      color: rgb(102, 178, 255);
      background-color: rgba(19, 47, 76, 0.676);
    }
    @media (max-width: 1100px) {
      font-size: 15px;
    }
  }

  @media (max-width: 1100px) {
    position: absolute;
    transform: translateX(-150%);
    background-color: #0a1929c6;
    height: 100vh;
    top: 0;
    transition: transform 0.6s;
    left: 0;
    padding: 10px;
    padding-top: 20px;
    box-sizing: border-box;
    box-shadow: rgba(0, 0, 0, 0.883) 3px -2px 15px 2px;
    &.open {
      transition: transform 0.6s;
      transform: translateX(0%);
      z-index: 3;
    }
  }
}
