body {
  margin: 0;
  background-color: #0a1929;
  color: #f3f6f9;
  font-size: 1rem;
  line-height: 1.5625;
  height: 100%;
}

.app {
  .table {
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    display: grid;
    grid-template-columns: 200px 1fr;
    position: absolute;
    top: 15%;
    width: 100%;
    padding: 60px;
    padding-top: 0;
    gap: 30px;

    @media (max-width: 1100px) {
      display: flex;
      top: 0%;
      padding-top: 80px;
      padding-left: 30px;
      // margin-left: 20px;
      padding-bottom: 20px;
    }

    @media (max-width: 800px) {
      padding-top: 60px;
      padding-left: 25px;
    }

    @media (max-width: 560px) {
      padding-top: 40px;
      padding-left: 10px;
      padding-right: 20px;
      gap: 20px;
    }

    @media (max-height: 520px) {
      padding-top: 25px;
    }

    .hamburger-btn {
      z-index: 3;
      display: none;
      cursor: pointer;

      @media (max-width: 1100px) {
        // grid-column: 2;
        display: block;
      }
    }
  }

  .gradient {
    width: 100%;
    height: 100vh;
    position: absolute;
    background-image: linear-gradient(to right, #000, #00000023);

    @media (max-width: 520px) {
      background-image: linear-gradient(-170deg, #000, #00000023);
    }
  }

  .film-poster {
    display: block;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    vertical-align: middle;
    max-width: 100%;
  }

  .menu-overlay {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    background-image: linear-gradient(to right, #000, #000000aa);
    transition: opacity 0.6s;
    z-index: 1;
    cursor: pointer;
  }

  .error {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 40px;
    font-size: 20px;
  }
}

.loader {
  &.MuiBox-root.css-k008qs {
    margin: auto;
    margin-top: 100px;
    justify-content: center;
    scale: 120%;
    width: max-content;
  }
}
